        </van-cell>
        <van-cell :title="orderDetailData.order.orderType=='600101'?'续费日期':'扣款日期'" :value="`每月${orderDetailData.order.billDate}日`" />
      </van-cell-group>
    </div>

    <div class="order-content order-content-main">
      <van-cell-group>
        <div class="title">
          <h2>账单</h2>
        </div>
        <van-cell :title="`首付（${orderDetailData.merchandise.duration-billList.length}个月课程）`" class="list-header outdate">
          <span class="right-grid">
            <van-button type="primary hairline" v-if="orderDetailData.bill[0]">{{orderDetailData.bill[0].status==='100201'?'缴费失败':orderDetailData.bill[0].status==='000000'?'已缴费':'缴费中'}}</van-button>￥{{orderDetailData.bill[0]&&(orderDetailData.bill[0].amount/100).toFixed(2)}}
          </span>
        </van-cell>
        <van-cell :title="`${item.periodNum}/${billList.length}期`" :label="item.billDate" class="list-main outdate" v-for="(item, i) in billList" :key="i">
          <span class="right-grid middle-align">
            <van-button :class="item.status==='100201'" v-show="item.status!=='100101'">{{item.status==='100201'?'缴费失败':item.status==='000000'?'已缴费':'缴费中'}}</van-button>￥{{(item.amount/100).toFixed(2)}}
          </span>
        </van-cell>
        <!-- <van-cell title="2/6期" label="2018.10.10" class="list-main  outdate">
          <span class="right-grid middle-align"> ￥3500</span>
        </van-cell>
        <van-cell title="3/6期" label="2018.10.10" class="list-main">
          <span class="right-grid middle-align">￥3500</span>
        </van-cell> -->
      </van-cell-group>
    </div>

    <div class="order-content order-content-main acount">
      <van-cell-group>
        <van-cell title="课程金额" :value="`￥${(orderDetailData.order.originalPrice/100).toFixed(2)}`" />
        <van-cell title="首付优惠" :value="`-￥${(orderDetailData.order.preferentialPrice/100).toFixed(2)}`" />
        <van-cell title="课程总实付" :value="`￥${(orderDetailData.order.currentPrice/100).toFixed(2)}`" />
      </van-cell-group>
    </div>

    <div class="order-content order-content-main order-content-footer">
      <van-cell-group>
        <router-link to="/instalmentContract">
        <van-cell title="电子合同" class="list-main">
          <span class="bule">《分期支付协议》</span>
        </van-cell>
        </router-link>
        <!-- <van-cell title="订阅邮箱" class="list-main" is-link >
          <span class="disabled">未添加</span>
        </van-cell> -->
        <router-link :to="`/autoRenew/${orderDetailData.order.id}/${orderDetailData.order.autoRenew?1:0}/${orderDetailData.order.billDate}`">
        <van-cell title="自动续费" class="list-main" is-link v-show="orderDetailData.order.orderType=='600101'&&(orderDetailData.order.orderStatus==='000000'||orderDetailData.order.orderStatus==='100201')">
          <span class="disabled">{{orderDetailData.order.autoRenew?'打开':'关闭'}}</span>
        </van-cell>
        </router-link>
      </van-cell-group>
    </div>
    <div class="order-footer">
      <van-button>手动支付</van-button>
    </div>
  </div>
</template>

<script>
import { queryOrderDetail } from '@/api/index.js';
import { banks } from '@/assets/js/fields.js';

export default {
  name: 'orderDetail',
  props: ['orderId'],
  data() {
    return {
      bankcardName: '',
      orderList: {},
      orderDetailData: {},
      billList: {},
    };
  },
  mounted() {},
  computed: {},
  created() {
    this.initData();
  },
  /* RENEWING("000000","续费中"),
*CANCEL("100100","订单取消"),
*WAIT_PAY("100200","待支付首付")
RENEW_FAIL("100201","续费失败"); */
  methods: {
    initData() {
      queryOrderDetail(this.$route.params.orderId)
        .then((result) => {
          if (result) {
            this.orderDetailData = result;
            this.orderList = result.order;
            this.billList = result.bill.splice(1);
            this.bankcardName = banks[result.bankCard.code] || result.bankCard.code;
          }
        })
        .catch(err => console.error(err));
    },
  },
  components: {},
};
</script>
